/* 차트 스타일링 */

@font-face {
    font-family: 'Godo';
    font-style: normal;
    font-weight: 400;
    src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff') format('woff');
}

@font-face {
    font-family: 'Godo';
    font-style: normal;
    font-weight: 700;
    src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff') format('woff');
}

.godo * {
    font-family: 'Godo', sans-serif;
}

h2{
    font-family: 'Godo', sans-serif;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.store {
    text-align: center;
}

.category-list-vertical {
    display: flex;
    flex-direction: column; /* 세로 정렬을 위한 flex-direction */
    align-items: flex-start;
    margin-bottom: 20px;
}

.category-list-vertical button {
    padding: 10px 20px;
    background-color: #f0f0f0;
    border: 1px solid #ddd;
    cursor: pointer;
    margin-bottom: 10px; /* 버튼 사이 간격 */
    transition: background-color 0.3s ease;
    width: 100%; /* 버튼이 동일한 너비를 가지도록 */
}

.category-list-vertical button:hover {
    background-color: #e0e0e0;
}

.product-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.product-card {
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 15px;
    text-align: center;
}

.product-image {
    width: 100%;
    height: auto;
    max-height: 200px;
    object-fit: cover;
}

.pagination {
    margin-top: 20px;
}

.pagination button {
    padding: 10px 15px;
    margin: 5px;
    border: 1px solid #ddd;
    background-color: #f0f0f0;
    cursor: pointer;
}

.pagination .active {
    background-color: #ddd;
}
